.calendar {
  font-family: arial;
  font-size: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: repeat(7, 100px);
  grid-template-rows: repeat(6, 100px);
  grid-gap: 1px;
}

.calendar div {
  background-color: #eee;
  width: 100%;
  height: 100%;
  outline: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.calendar-mon div:first-of-type {
  grid-column-start: 1;
}
.calendar-tue div:first-of-type {
  grid-column-start: 2;
}
.calendar-wed div:first-of-type {
  grid-column-start: 3;
}
.calendar-thu div:first-of-type {
  grid-column-start: 4;
}
.calendar-fri div:first-of-type {
  grid-column-start: 5;
}
.calendar-sat div:first-of-type {
  grid-column-start: 6;
}
.calendar-sun div:first-of-type {
  grid-column-start: 7;
}

.calendar div:nth-of-type(1)::before {
  content: "1";
